:root {
  --primary-color: #0074b7; /* Blue from logo */
  --secondary-color: #28a745; /* Green from logo */
  --accent-color: #f1f8f5; /* Light water-inspired background */
  --text-color: #333333; /* Dark grey for readability */
  --light-text-color: #ffffff; /* White for contrast */
  --shadow-color: rgba(0, 0, 0, 0.1); /* Soft shadow for depth */
}

/* General Styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background: #ffffff;
}

/* Hero Section */
.hero {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.hero-text {
  max-width: 40%;
  padding: 60px;
}

.hero-text h1 {
  font-size: 80px;
  font-weight: bold;
}

.hero-text p {
  font-size: 18px;
  color: var(--text-color);
}

.buttons {
  margin-top: 20px;
}
button {
  border: 2px solid var(--secondary-color);
}
.btn {
  padding: 16px 32px;
  border-radius: 100px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  margin-right: 10px;
}

.primary {
  background: var(--secondary-color);
  color: var(--light-text-color);
}

.secondary {
  background: var(--light-text-color);
  border: 2px solid var(--secondary-color);
  color: var(--secondary-color);
}

/* Hero Image */
.hero-image img {
  width: 100%;
  max-width: 350px;
}

/* Responsive Styles */
@media (max-width: 1024px) {
  .hero {
    flex-direction: column;
    text-align: center;
  }
  .hero-text {
    max-width: 100%;
  }
  .hero-image img {
    max-width: 300px;
  }
}

@media (max-width: 768px) {
  .hero-text h1 {
    font-size: 28px;
  }
  .hero-text p {
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .hero-text h1 {
    font-size: 24px;
  }

  .hero-text p {
    font-size: 14px;
  }
}

.about-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--accent-color);
  width: 100%;
}

.about-content {
  flex: 1;
  padding: 20px;
}

.about-content h2 {
  font-size: 24px;
  font-weight: bold;
  color: var(--secondary-color);
}

.about-content p {
  font-size: 14px;
  color: var(--text-color);
  line-height: 1.5;
}

.learn-more {
  color: var(--text-color);
  border: 2px solid var(--secondary-color);
  margin-right: 10px;
}

.get-in-touch {
  background: var(--secondary-color);
  color: var(--light-text-color);
  border: none;
}

.about-image {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}

.about-image img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 1024px) {
  .about-section {
    flex-direction: column;
  }
  .about-content {
    padding: 10px;
  }
  .about-content h2 {
    font-size: 20px;
  }
  .about-content p {
    font-size: 12px;
  }
}

@media (max-width: 768px) {
  .about-section {
    padding: 10px;
  }
  .about-content {
    padding: 5px;
  }
  .about-content h2 {
    font-size: 18px;
  }
  .about-content p {
    font-size: 10px;
  }
  .about-image img {
    height: 200px;
  }
}

@media (max-width: 480px) {
  .about-section {
    padding: 5px;
  }
  .about-content {
    padding: 5px;
  }
  .about-content h2 {
    font-size: 16px;
  }
  .about-content p {
    font-size: 9px;
  }
  .about-image img {
    height: 150px;
  }

  .about-content h2 {
    font-size: 18px;
  }

  .about-content p {
    font-size: 10px;
  }
}

/* Services Section */
.services {
  padding: 50px;
  background: white;
  text-align: left;
}

.services h2 {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 20px;
  color: var(--primary-color);
}

.services-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  background: var(--accent-color);
  padding: 20px;
  margin: 20px;
  border-radius: 10px;
  box-shadow: 0px 5px 10px var(--shadow-color);
  width: calc(25% - 20px);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.card img {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  margin-bottom: 20px;
}

.card h3 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.card p {
  font-size: 18px;
  color: var(--text-color);
}

/* Footer */
footer {
  padding: 20px;
  background: var(--secondary-color);
  color: var(--light-text-color);
  text-align: center;
}

.footer-text p {
  font-size: 18px;
  margin-bottom: 10px;
}

.social-links {
  margin-top: 20px;
}

.social-links a {
  margin: 0 10px;
  font-size: 24px;
  color: white;
}

.social-links a:hover {
  color: #f7f7f7;
}

/* Responsive Styles */
@media (max-width: 1024px) {
  .card {
    width: calc(50% - 20px);
  }
  .testimonial-card {
    width: calc(50% - 20px);
  }
}

@media (max-width: 768px) {
  .card {
    width: 100%;
  }
  .testimonial-card {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .card {
    width: 100%;
  }
  .testimonial-card {
    width: 100%;
  }
}
