.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 1000;
  background: white;
  transition: all 0.4s ease;
  padding: 0 20px;
}

.header.scrolled {
  backdrop-filter: blur(15px);
  background: rgba(255, 255, 255, 0.9);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  padding: 10px 20px;
}

.logoContainer {
  display: flex;
  align-items: center;
}

.logo {
  width: 120px; /* Adjust as needed */
  transition: transform 0.3s ease;
}

.logoLink:hover .logo {
  transform: scale(1.05);
}

.nav {
  position: relative;
}

.navList {
  display: flex;
  gap: 40px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.navItem {
  position: relative;
  perspective: 1000px;
}

.navLink {
  text-decoration: none;
  color: var(--secondary-color, #16213e);
  font-size: 18px;
  font-weight: 600;
  padding: 8px 16px;
  position: relative;
  transition: color 0.3s ease, transform 0.3s ease;
}

.navLink:hover {
  transform: translateY(-2px);
}

.navEffect {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 2px;
  background: var(--secondary-color, #16213e);
  transition: width 0.3s ease, left 0.3s ease;
}

.navEffectActive {
  width: 100%;
  left: 0;
}

.subMenuToggle {
  border: none;
  background: none;
  cursor: pointer;
  font-size: 14px;
  color: var(--secondary-color, #16213e);
  padding: 0 5px;
  transition: transform 0.3s ease;
}

.subMenuToggle:hover {
  transform: rotate(180deg);
}

.subMenu {
  position: absolute;
  top: 100%;
  right: 50%;
  transform: translateX(50%);
  width: 300%;
  background: #fff;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15);
  border-radius: 12px;
  overflow: hidden;
  z-index: 9999;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, transform 0.3s ease;
  transform-origin: top center;

  li {
    list-style: none;
    text-decoration: none;
  }
  ul {
    padding: 0;
  }
}

.subMenuVisible {
  opacity: 1;
  visibility: visible;
  transform: translateX(50%) scale(1);
}

.subMenuItem {
  padding: 0;
  transition: transform 0.2s ease;
}

.subMenuItem:hover {
  transform: translateX(5px);
}

.subMenuLink {
  text-decoration: none;
  color: var(--secondary-color, #16213e);
  font-size: 16px;
  display: block;
  padding: 12px 20px;
  transition: background 0.3s ease, color 0.3s ease;
}

.subMenuLink:hover {
  background: var(--accent-color, #ff6b6b);
  color: #333;
  background-color: var(--secondary-color);
}

.subMenu li:not(:last-child) {
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.ctaBtn {
  padding: 12px 24px;
  background: var(--secondary-color);
  color: #fff;
  border: none;
  border-radius: 100px;
  font-size: 16px;
  font-weight: 600;
  margin-right: 40px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.ctaBtn:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.ctaRipple {
  position: absolute;
  width: 0;
  height: 0;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  animation: ripple 0.6s ease-out;
}

@keyframes ripple {
  to {
    width: 300px;
    height: 300px;
    opacity: 0;
  }
}

.copyMessage {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background: var(--accent-color, #ff6b6b);
  color: #fff;
  padding: 10px 20px;
  border-radius: 8px;
  animation: fadeInOut 2s ease;
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  20% {
    opacity: 1;
    transform: translateY(0);
  }
  80% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(20px);
  }
}

.menuToggle {
  display: none;
  cursor: pointer;
  margin-right: 40px;
}

.hamburger {
  width: 30px;
  height: 20px;
  position: relative;
}

.hamburger span {
  position: absolute;
  width: 100%;
  height: 3px;
  background: var(--secondary-color, #16213e);
  transition: all 0.4s ease;
}

.hamburger span:nth-child(1) {
  top: 0;
}
.hamburger span:nth-child(2) {
  top: 8px;
}
.hamburger span:nth-child(3) {
  top: 16px;
}

.hamburger.active span:nth-child(1) {
  transform: rotate(45deg) translate(6px, 6px);
}

.hamburger.active span:nth-child(2) {
  opacity: 0;
  transform: scale(0);
}

.hamburger.active span:nth-child(3) {
  transform: rotate(-45deg) translate(7px, -7px);
}

/* Responsive Design */
@media (max-width: 768px) {
  .menuToggle {
    display: block;
  }

  .nav {
    position: fixed;
    top: 0;
    right: -100%;
    width: 80%;
    height: 100vh;
    background: #16213e;
    transition: right 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55);
    padding: 20px;
  }

  .navActive {
    right: 0;
  }

  .navList {
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    height: 100%;
    gap: 20px;
  }

  .navLink {
    color: #fff;
    font-size: 22px;
  }

  .subMenu {
    position: static;
    transform: none;
    width: 100%;
    background: rgba(255, 255, 255, 0.1);
    box-shadow: none;
    border-radius: 8px;
    margin-top: 10px;
  }

  .subMenuLink {
    color: #fff;
    font-size: 18px;
  }

  .ctaBtn {
    position: absolute;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
    background: var(--secondary-color);
  }
}

@media (max-width: 480px) {
  .ctaBtn {
    font-size: 14px;
    padding: 10px 20px;
  }

  .navLink {
    font-size: 20px;
  }
}
