.btn-floating.fixed-bottom {
  position: fixed;
  top: auto;
  bottom: 25px;
  right: 20px;
  border: 2px solid #f1f1f1;
}

.btn-floating:hover,
.btn-floating:focus {
  background-color: #3e9e39;
  border: 2px solid #f1f1f1;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.4);
  transform: scale(1.1);
}
a:active,
a:hover {
  outline: 0;
}
a:focus,
a:hover {
  color: #005580;
  text-decoration: underline;
}
a:focus,
a:hover {
  color: #434343;
  text-decoration: none;
  outline: none;
  box-shadow: none;
}

.btn-floating {
  position: absolute;
  top: 0;
  right: 0;
  padding: 5px 8px;
  color: rgba(67, 67, 67, 0.7);
  fill: rgba(67, 67, 67, 0.7);
  background-color: #ffffff;
  border: 1px solid rgba(67, 67, 67, 0.05);
  border-radius: 30px;
  font-size: 18px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.04),
    0 3px 1px -2px rgba(0, 0, 0, 0.04), 0 1px 5px 0 rgba(0, 0, 0, 0.04);
}

.btn-whatsapp svg {
  width: 30px;
  height: 30px;
  padding: 5px 2px;
  fill: white;
  vertical-align: middle;
}
.btn-whatsapp {
  color: white;
  background-color: #4dc247 !important;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.4);
  border-radius: 50%;
  animation: pulse 2s infinite;
}
user agent stylesheet svg:not(:root),
symbol,
image,
marker,
pattern,
foreignObject {
  overflow: hidden;
}

@keyframes pulse {
  0% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(77, 194, 71, 0.7);
  }
  70% {
    transform: scale(1.1);
    box-shadow: 0 0 10px 10px rgba(77, 194, 71, 0);
  }
  100% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(77, 194, 71, 0);
  }
}
