.cosmicFooter {
  position: relative;
  width: 100%;
  background: var(--secondary-color);
  color: #fff;
  padding: 60px 0;
  overflow: hidden;
  box-shadow: 0 -10px 20px rgba(0, 212, 255, 0.3);
  text-align: left;
}
a {
  text-decoration: none;
  color: #fff;
}
li {
  list-style: none;
  padding: 0;
  margin: 0;
}
.footerContent {
  position: relative;
  z-index: 2;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.footerSection {
  flex: 1;
  min-width: 200px;
  margin: 15px;
  transition: transform 0.3s ease;
  text-align: left;
}

.footerSection:hover {
  transform: translateY(-5px);
}

.footerSection h3 {
  font-size: 18px;
  margin-bottom: 15px;
  color: #00d4ff;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: left;
}

.footerSection h3 i {
  margin-right: 8px;
  color: #fff;
}

.footerSection p,
.footerSection ul {
  font-size: 14px;
  opacity: 0.9;
  margin: 0;
  text-align: left;
}

.footerSection ul {
  list-style: none;
  padding: 0;
}

.footerSection ul li {
  margin: 8px 0;
  text-align: left;
}

.footerSection a {
  color: #fff;
  text-decoration: none;
  transition: all 0.3s ease;
  text-align: left;
}

.footerSection a:hover {
  color: #00d4ff;
  text-shadow: 0 0 5px rgba(0, 212, 255, 0.7);
}

.contactInfo p i {
  margin-right: 10px;
  font-size: 16px;
  color: #00d4ff;
  text-align: left;
}

.contactInfo a {
  color: #00d4ff;
  text-decoration: none;
  cursor: pointer;
}

.contactInfo a:hover {
  text-decoration: underline;
}

.socialLinks {
  margin-top: 15px;
  text-align: left;
}

.socialIcon {
  color: #fff;
  text-decoration: none;
  font-size: 24px;
  margin: 0 15px;
  transition: all 0.3s ease;
  text-align: left;
}

.socialIcon:hover {
  transform: scale(1.3) rotate(10deg);
  color: #00d4ff;
  text-shadow: 0 0 10px rgba(0, 212, 255, 0.8);
}

.copyPhone {
  display: none;
  cursor: pointer;
  color: #00d4ff;
  margin-left: 10px;
}

.copyPhone:hover {
  text-decoration: underline;
}

@media (min-width: 1024px) {
  .copyPhone {
    display: inline;
  }
}

.copyright {
  width: 100%;
  margin-top: 30px;
  font-size: 14px;
  opacity: 0.7;
  letter-spacing: 1px;
}

/* Dual Wave Animation */
.wave {
  position: absolute;
  left: 0;
  width: 100%;
  height: 120px;
  background-size: cover;
  z-index: 1;
}

.wave1 {
  top: -60px;
  background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320"><path fill="rgba(0,212,255,0.2)" d="M0,224L48,213.3C96,203,192,181,288,186.7C384,192,480,224,576,229.3C672,235,768,213,864,202.7C960,192,1056,192,1152,197.3C1248,203,1344,213,1392,218.7L1440,224L1440,320L1392,320C1344,320,1248,320,1152,320C1056,320,960,320,864,320C768,320,672,320,576,320C480,320,384,320,288,320C192,320,96,320,48,320L0,320Z"></path></svg>');
  animation: wave1 8s infinite linear;
}

.wave2 {
  top: -40px;
  background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320"><path fill="rgba(255,255,255,0.1)" d="M0,160L48,149.3C96,139,192,117,288,133.3C384,149,480,203,576,208C672,213,768,171,864,149.3C960,128,1056,128,1152,144C1248,160,1344,192,1392,208L1440,224L1440,320L1392,320C1344,320,1248,320,1152,320C1056,320,960,320,864,320C768,320,672,320,576,320C480,320,384,320,288,320C192,320,96,320,48,320L0,320Z"></path></svg>');
  animation: wave2 10s infinite linear reverse;
}

@keyframes wave1 {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(-30%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes wave2 {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(25%);
  }
  100% {
    transform: translateX(0);
  }
}

/* Stars Overlay */
.starsOverlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
    circle,
    rgba(255, 255, 255, 0.05) 1px,
    transparent 1px
  );
  background-size: 20px 20px;
  opacity: 0.5;
  z-index: 1;
  animation: twinkle 5s infinite;
}

@keyframes twinkle {
  0%,
  100% {
    opacity: 0.5;
  }
  50% {
    opacity: 0.8;
  }
}

/* Responsive Adjustments */
@media (max-width: 600px) {
  .footerSection {
    flex: 100%;
  }
  .socialIcon {
    margin: 0 10px;
  }
}

@media (max-width: 768px) {
  .footerContent {
    flex-direction: column;
    align-items: flex-start;
    grid-template-columns: 1fr;
  }
  .footerSection {
    margin: 10px 0;
  }
}

.credits {
  background-color: #222;
  color: white;
  font-size: 1.2rem;
  padding: 10px;
  text-align: left;
  a {
    color: white;
    text-decoration: none;
    font-weight: 600;
  }
}
