/* Services.module.css */

.servicesSection {
  padding: 20px 20px;
  background: #f9f9f9;
  justify-content: center;
  padding-top: 100px;
  padding-bottom: 100px;
}

.container {
  max-width: 1700px;
  margin: 0 auto;
}

.heading {
  font-size: 2.5rem;
  font-weight: bold;
  color: #333;
  margin-bottom: 40px;
}

.servicesGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  justify-content: center;
  align-items: center;
}

.card {
  background: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
}

.card:hover {
 box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1), 0 -4px 10px rgba(0, 0, 0, 0.1);
  transform: translateY(-40px);
}

.image {
  width: 100%;
  height: auto;
  border-radius: 10px;
}

.cardContent {
  padding: 15px 0;
}

.title {
  font-size: 1.5rem;
  color: #222;
  margin-bottom: 10px;
}

.description {
  font-size: 1rem;
  color: #555;
}
.button {
  width: 100%;
  padding: 15px;
  border-radius: 50px;
  background-color: #4caf50;
  color: #fff;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
  a {
    text-decoration: none;
    color: #fff;
  }
}

.button:hover {
  background-color: #3e8e41;
}

@media (max-width: 1024px) {
  .servicesGrid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
}

@media (max-width: 768px) {
  .servicesGrid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .servicesGrid {
    grid-template-columns: 1fr;
  }
}
