/* Hero Section */
.hero {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 50px;
}
a {
  text-decoration: none;
}
.heroText {
  max-width: 40%;
  padding: 20px;
}

.heroText h1 {
  font-size: 4.5rem;
  font-weight: bold;
}

.heroText p {
  font-size: 1.5rem;
  color: var(--text-color);
}

.buttons {
  margin-top: 20px;
}
.heroImage {
  width: 50%;
  height: 90vh;
}
.heroImage img {
  width: 100%;
  max-width: 350px;
}

/* About us section */
.aboutSection {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--accent-color);
  box-shadow: 0px -20px 20px 1px rgba(0, 0, 0, 0.1);
}

.aboutContent {
  flex: 1;
  padding: 20px;
  margin-right: 20px;
}

.aboutContent h2 {
  font-size: 2.5rem;
  font-weight: bold;
  color: var(--secondary-color);
  margin-bottom: 10px;
}

.aboutContent p {
  font-size: 1.2rem;
  color: var(--text-color);
  line-height: 1.5;
  margin-bottom: 20px;
}

.aboutImage {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}

.aboutImage img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 10px 0 0 10px;
}

/* Media Queries */
@media (max-width: 1024px) {
  .heroText {
    max-width: 100%;
    text-align: center;
    margin-top: 100px;
  }

  .heroImage {
    margin-top: 0;
  }

  .aboutSection {
    flex-direction: column;
  }

  .aboutContent {
    padding: 10px;
  }

  .card {
    width: calc(50% - 40px);
  }
}

@media (max-width: 768px) {
  .heroText h1 {
    font-size: 2rem;
  }

  .heroText p {
    font-size: 1rem;
  }

  .aboutContent h2 {
    font-size: 1.5rem;
  }

  .aboutContent p {
    font-size: 0.9rem;
  }

  .card {
    width: 100%;
  }
  .heroImage {
    width: 100%;
    height: 80vh;
  }
  .hero {
    padding: 0;
  }

  .aboutImage img {
    max-width: 100%;
    height: auto;
    object-fit: cover;
    margin-top: 20px;
    border-radius: 16px;
  }
}

@media (max-width: 480px) {
  .heroText h1 {
    font-size: 2rem;
  }

  .heroText p {
    font-size: 1rem;
  }

  .aboutContent h2 {
    font-size: 1.3rem;
  }

  .aboutContent p {
    font-size: 0.8rem;
  }
  .buttons {
    width: 100%;
    display: flex;
    margin: 0 auto;
    justify-content: space-between;
  }
}
