.testimonials {
  padding: 50px 20px;
  background: var(--accent-color);
  text-align: center;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 60vh;
}

.testimonials h2 {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 20px;
  color: var(--primary-color);
}

.sliderContainer {
  position: relative;
  width: 80%;
  max-width: 1200px;
  margin: 0 auto;
  overflow: hidden;
}

.testimonialSlider {
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.testimonialCard {
  flex: 0 0 100%;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px var(--shadow-color);
  background-color: white;
}

.testimonialCard p {
  font-size: 1rem;
  color: var(--text-color);
  line-height: 1.6;
}

.testimonialCard h4 {
  font-size: 1.1rem;
  font-weight: bold;
  color: var(--text-color);
  margin-top: 15px;
}

.navigation {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
}

.navButton {
  background-color: rgba(0, 0, 0, 0.5);
  color: var(--light-text-color);
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
}

.navButton:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

@media (max-width: 1024px) {
  .testimonials {
    padding: 40px 15px;
  }
  .testimonials h2 {
    font-size: 1.8rem;
  }
  .testimonialCard p {
    font-size: 1rem;
  }
  .testimonialCard h4 {
    font-size: 1.1rem;
  }
  .navButton {
    padding: 9px 13px;
    font-size: 0.95rem;
  }
}

@media (max-width: 768px) {
  .testimonials {
    padding: 30px 10px;
  }
  .testimonials h2 {
    font-size: 1.5rem;
  }
  .testimonialCard p {
    font-size: 0.9rem;
  }
  .testimonialCard h4 {
    font-size: 1rem;
  }
  .navButton {
    padding: 8px 12px;
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .testimonials {
    padding: 20px 5px;
  }
  .testimonials h2 {
    font-size: 1.2rem;
  }
  .testimonialCard p {
    font-size: 0.8rem;
  }
  .testimonialCard h4 {
    font-size: 0.9rem;
  }
  .navButton {
    padding: 7px 10px;
    font-size: 0.85rem;
  }
}
