.slider {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: 10s slider linear infinite;
  background-repeat: no-repeat;
  background-size: 60% auto;
  background-position: center;
}
.slider::before {
  content: "";
  position: absolute;
  top: 55%;
  right: 30%;
  transform: translate(50%, -50%);
  width: 35%;
  height: 70%;
  background-color: #29b554;
  border-radius: 30% 10% 50% 20%;
  z-index: -1;
}
/* Responsive design for tablets */
@media (max-width: 768px) {
  .slider {
    width: 100%;
    height: 50vh;
    background-size: 80% auto;
  }
}

/* Responsive design for mobile devices */
@media (max-width: 480px) {
  .slider {
    width: 95%;
    height: 40vh;
    background-size: 100% auto;
  }

  .slider::before {
    content: "";
    position: absolute;
    top: 120%;
    right: 50%;
    transform: translate(50%, -50%);
    width: 90%;
    height: 60%;
    background-color: #29b554;
    border-radius: 40% 10% 15% 10%;
    z-index: -1;
  }
}

@media (max-width: 1024px) {
  .slider {
    height: 60vh;
    background-size: 70% auto;
  }
}

@media (max-width: 480px) {
  .slider {
    height: 80vh;
    background-size: 100% auto;
  }
}

@keyframes slider {
  0%,
  15% {
    background-image: url("../../images/images1.png");
  }
  20%,
  35% {
    background-image: url("../../images/image2.png");
  }
  40%,
  55% {
    background-image: url("../../images/image3.png");
  }
  60%,
  80% {
    background-image: url("../../images/image4.png");
  }
  80%,
  95% {
    background-image: url("../../images/image5.png");
  }
  100% {
    background-image: url("../../images/images1.png");
  }
}
